export const GET_ROUTER_MENU_SAGA = 'GET_ROUTERMENU';
export const RECEIVED_ROUTER_MENU_DATA = 'RECEIVED_ROUTER_MENU_DATA';
export const ROUTER_MENU_ISFINISHED = 'ROUTER_MENU_ISFINISHED';

export const getRouterMenu = (params) => {

    return {
        type: GET_ROUTER_MENU_SAGA,
        params,
    }
}

export const receivedRouterMenuData = (params) => {
    return {
        type: RECEIVED_ROUTER_MENU_DATA,
        params,
    }
}

export const isFinished = (params) => {
    return {
        type: ROUTER_MENU_ISFINISHED,
        params,
    }
}


